'use client';
import { LayoutGroup, motion } from 'framer-motion';
import { SparklesCore } from '@/components/ui/SparklesCore'; // adjust path if needed

export default function HeroSection() {
  const brands = ['ramp', 'hex', 'vercel', 'descript', 'cashapp', 'notion',
    'mercury', 'retool', 'remote', 'arc', 'raycast', 'runway'];

  return (
        <div className="min-h-screen w-full bg-[linear-gradient(to_bottom,_#1B2B40_0%,_#0e0e0e_60%)] text-white font-sans px-6 md:px-12 overflow-hidden relative">

            <div className="absolute top-0 left-1/2 -translate-x-1/2 w-[80vw] h-[80vh] bg-[radial-gradient(ellipse_at_top,_#1B2B40_0%,_transparent_70%)] opacity-60 blur-2xl pointer-events-none z-0" />

      
      {/* Content Layer */}
      <div className="relative z-10">
        {/* Navbar */}
        <div className="flex justify-between items-center py-2">
    <img
      src="/icons/majarahlogo.svg"
      alt="Majarah Logo"
      className="w-60 h-auto"
    />

    {/* Navigation with hover effect */}
    <LayoutGroup id="navbar-links">
      <div className="hidden md:flex space-x-2 text-sm relative">
        {["About Us", "Services", "Work", "Project"].map((item, index) => (
          <motion.a
            key={index}
            href="#"
            whileHover="hover"
            className="relative px-4 py-2 text-white/80"
          >
            <motion.div
              layoutId="navbar-hover"
              transition={{
                type: "spring",
                stiffness: 500,
                damping: 30,
              }}
              className="absolute inset-0 z-0 rounded-full bg-white/10"
            />
            <span className="relative z-10">{item}</span>
          </motion.a>
        ))}
      </div>
    </LayoutGroup>

    {/* Contact Us Button */}
    <a
      href="#"
      className="bg-white/10 border border-white/20 px-4 py-2 text-sm rounded-full hover:bg-white/20 transition mr-10"
    >
      Contact Us
    </a>
  </div>

        {/* ‚≠ê Hero Text Section - limited sparkle background */}
        <div className="relative mt-16 md:mt-24 max-w-3xl mx-auto text-center z-10">
          
          {/* ‚ú® Sparkles just behind the hero text */}
          <SparklesCore
            className="absolute left-1/2 -translate-x-1/2 h-full w-[180%] z-0"
            particleColor="#3ABEFF"
            particleDensity={200}   // reduced
            minSize={0.3}
            maxSize={0.7}
            speed={1.5}
            background="transparent"
          />

          {/* Hero Text Content */}
          <div className="relative z-10 ">
            <p className="text-xs px-4 py-1 rounded-full border border-white/20 inline-block text-white/60 mb-6">
              Creating Digital That Matters
            </p>

            <motion.h1
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 1 }}
              className="text-4xl sm:text-5xl md:text-6xl font-semibold leading-tight"
            >
              We Don‚Äôt <span className="text-[#3ABEFF]">Just Create Content</span>
              <br />
              We Build <span className="text-[#3ABEFF]">Impact</span>
            </motion.h1>

            <p className="text-sm text-gray-400 mt-6 leading-relaxed">
              Founded by Eisa Al Habib, Emmy¬Æ award-winning filmmaker and Forbes 30 Under 30 honoree,<br />
              Majarah is a full-scale media agency delivering next-level storytelling, influencer-driven campaigns,<br />
              and cross-platform digital marketing that moves people‚Äîand brands.
            </p>

            <motion.a
              href="#"
              whileHover={{ scale: 1.05 }}
              className="inline-block mt-8 px-6 py-3 bg-gradient-to-r from-[#2A2B5F] to-[#3ABEFF] text-white rounded-full shadow-md hover:shadow-lg transition-all duration-300"
            >
              Let‚Äôs Work Together ‚Üí
            </motion.a>
          </div>
        </div>

        {/* Logos */}
        <div className="mt-20 text-center">
          <p className="text-sm text-gray-400 mb-6">
            Top-tier brands and governments redefining how stories are told online.
          </p>
          <div className="grid grid-cols-3 sm:grid-cols-4 md:grid-cols-6 gap-6 place-items-center max-w-5xl mx-auto">
            {brands.map((brand, i) => (
              <img
                key={i}
                src={`/brands/${brand}.svg`}
                alt={brand}
                className="h-5 grayscale opacity-70 hover:opacity-100 transition"
              />
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}
