"use client";

import React, { useEffect, useState, useId } from "react";
import Particles, { initParticlesEngine } from "@tsparticles/react";
import { loadSlim } from "@tsparticles/slim";
import { motion, useAnimation } from "framer-motion";
import type { Container } from "@tsparticles/engine";

// ‚úÖ Props Interface
type SparklesProps = {
  className?: string;
  particleColor?: string;
  particleDensity?: number;
  minSize?: number;
  maxSize?: number;
  particleCount?: number;
  speed?: number;
  background?: string;
};

export const SparklesCore: React.FC<SparklesProps> = ({
  className = "",
  particleColor = "#00ffff", // Neon cyan
  minSize = 0.3,
  maxSize = 0.7,
  particleCount = 300,
  speed = 0.1,
}) => {
  const [init, setInit] = useState(false);
  const controls = useAnimation();
  const generatedId = useId();

  useEffect(() => {
    initParticlesEngine(async (engine) => {
      await loadSlim(engine);
    }).then(() => setInit(true));
  }, []);

  const particlesLoaded = async (container?: Container) => {
    if (container) {
      controls.start({
        opacity: 1,
        transition: { duration: 1 },
      });
    }
  };

  return (
    <motion.div
      animate={controls}
      className={`absolute inset-0 z-0 opacity-0 pointer-events-none ${className}`}
    >
      {init && (
        <Particles
          id={generatedId}
          className="h-full w-full"
          particlesLoaded={particlesLoaded}
          options={{
            fullScreen: { enable: false },
            background: { color: { value: "transparent" } },
            fpsLimit: 60,
            detectRetina: true,
            particles: {
              number: {
                value: particleCount,
                density: { enable: false },
              },
              color: { value: particleColor },
              size: {
                value: { min: minSize, max: maxSize },
              },
              opacity: {
                value: { min: 0.1, max: 0.8 }, // Slightly dimmer for glow
                animation: {
                  enable: true,
                  speed: 2,
                  startValue: "random",
                  destroy: "none",
                },
              },
              shadow: {
                enable: true,
                color: particleColor,
                blur: 5, // Glow intensity
              },
              move: {
                enable: true,
                speed: speed,
                direction: "bottom", // moving down
                outModes: { default: "out" },
              },
              shape: { type: "circle" },
            },
          }}
        />
      )}
    </motion.div>
  );
};
